$main-color: #f06292;
// $main-color: #1976d2;
$highlight-color: #ef5350;

*{
    margin:0px;
    padding: 0px;
    font-family: -apple-system,".SFNSText-Regular","San Francisco","Roboto","Segoe UI","Helvetica Neue","Lucida Grande",Arial,sans-serif;
}
body{
    transition: ease .3s;
    background: #fbfbfb;
    
}

header{height: 50px;}
footer{
    background: #212121;
    padding: 40px;
    font-size: 0.8rem;
    color: #888;
}
.wrapper{
    // padding: 20px 10%;
    box-sizing: border-box;
    max-width: 925px;
    margin: auto;
    position: relative;
}
.fixed-top{
    position: sticky;
    top:20px;
}
.row{
    display: table;
    width: 100%;
    border-collapse: separate;
    border-spacing: 10px 5px;
    .col{
        width: 100%;
        height: 100%;
    }
    .col-1-4, .col-3-4, .col-1-5, .col-4-5{
        display: table-cell;
        // vertical-align: middle;
    }
    .col-1-4{
        width: 25%;
    }
    .col-3-4{
        width: 75%;
    }
    .col-1-5{
        width: 20%;
    }
    .col-4-5{
        width: 80%;
    }
}
div.highlight{
    background-color: $highlight-color;
    border: 1px solid black;
}
span.highlight{
    color: $highlight-color;
}

.block{
    min-height: 100px;
    .sec{
        height: 100%;
        // background-color: #ccc;
        .title{
            position: relative;
            font-size: 1.6rem;
            margin: 20px 15px;
            font-weight: bold;
            display: flex;
            align-items: center;
            // font-family: sans;
            .t_icon{
                height: 1.5rem;
                width: 1.5rem;
                position: absolute;
                left: -2rem;
                opacity: 0;
                transition: 0.2s ease;
            }
        }
        .content{
            text-align: justify;
        }
    }
    .sec:hover{
        .t_icon{opacity: 1;}
    }
}

.img-board{
    width: 50px;
    height: 50px;
    margin:5px auto;
    position: relative;
    .img, .img2, .cover{
        position: absolute;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 100px;
        transition: ease .3s;
    }
    .img{
        top:0px;
        left: 0px;
        z-index: 2;
        // border: 1px solid #DDD;
        box-sizing: border-box;
    }
    .img2{
        top:0px;
        left: 0px;
        z-index: 3;
        // border: 1px solid #DDD;
        box-sizing: border-box;
    }
    .cover{
        opacity: 0;
        top:10px;
        left: 0px;
        z-index: 1;
        filter:blur(20px);
    }
}
.img-board:hover{
    .img,.img2{
        transform: scale(1.05);
        border:0px;
    }
    .cover{
        opacity: 0.8;
        filter: blur(30px);
    }
}

.profile{
    font-size: 1.1rem;
    // *{font-family: serif !important;}
    transition: ease .3s;
    background-color: white;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 1em;
    // overflow: hidden;
    // box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
    background-color: transparent !important;
    .row{max-width: 180px;margin: auto;}
    .zh_name{transition: ease .3s;}
    .img-board{
        width: calc(100% - 10px);
        max-width: 180px;
        height: auto;
        margin-bottom: 20px;
        aspect-ratio: 1/1;
        *{border-radius: 1rem;opacity: 1;border:none;}
        .img2{
            position: absolute;
        }
    }
    .name-board, .contact-board{
        border-spacing: 0px;
        text-align: left;
    }
    .name{
        font-weight: bold;
        font-size: 1.6rem;
    }
    .item{font-size: 0.8rem;}
    .dot{
        position: relative;
        height:10px;
    }
    .dot::before{
        content: "";
        height: 8px;
        width: 8px;
        margin: 1px;
        position: absolute;
        background: black;
        border-radius: 10px;
    }
    
    .line{
        height:0px;
        margin:10px auto;
        border-bottom: 1px solid #ddd;
    }
    .email, .github, .location, .scholar{
        font-size: 14px;
        line-height: 24px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        .logo{
            height: 20px;
            width: 20px;
            margin:4px;
            margin-right: 10px;
        }
    }
}

.logo{
    height: 24px;
    width: 24px;
    background-color: transparent;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    margin: auto;
    // background-color: #EEE;
    border-radius: 5px;
    border: none;
    transition: 0.2s ease;
}
a.logo:hover{
    background-color:$main-color;
    filter:invert(1) hue-rotate(180deg);
}


ul{
    list-style: none;
}
li{
    margin:5px;
    padding: 5px 10px;
    box-sizing: border-box;
    transition: ease .3s;
    background-color: white;
    border-radius: 10px;
    border-spacing:0px !important;
}
.news{
    li{
        .date{float:left; font-weight: bold;}
        .event{
            text-indent: 10px;
        }
    }
}
.honor{
    li{
        .date{float:right;}
        .date, .event{font-weight: bold;}
        // .event{
        //     text-indent: 10px;
        // }
    }
}

.education{
    li{
        float: left;
        position: relative;
        width: calc(33% - 10px);
        // padding: 5px 0px;
        padding-top: 10px;
        transition: ease .3s;
        // box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
        *{font-size: 0.9rem;}
        .ins{
            font-weight: bold;
            font-size: 1.1rem;
        }
        .duration{margin-top:10px;color: #bbb;font-size:0.8rem;}
        .img-board{
            width: 80px;
            height: 80px;
            *{
                background-size: contain;
                border-radius: 0px;
                border:none;
            }
        }
        .remark-b{
            margin-top:5px;background-color: #fbfbfb;padding:0px;border-radius:5px;color:#AAA;
            // *{margin:5px;}
        }
    }
}

.paper{
    .touch-board{
        position: absolute;
        display: none;
        top: 0;
        right: calc(100% - 5px);
        padding-right: 10px;
        width: auto;
        margin: 0;
        text-align: right;
        img{
            height: 100px;
            right: 0;
            border-radius: 10px;
            box-shadow: 0px 10px 10px -3px rgba(0,0,0,0.1);
        }
        .links{
            width: 4rem;
            height: 1.8rem;
            display: flex;
            float: right;
            .logo{
                background-size: 80%;
                background-color: white;
                border-radius: 10px;
                height: 1.5rem;
                width: auto;
                aspect-ratio: 1/1;
                border-bottom: none !important;
            }
        }
    }
    li:hover .touch-board{display: block;}
    *{font-size: 0.9rem;}
    ul .title{
        font-size: 1.1rem !important;
        margin: 5px 0px !important; 
        line-height: 1.1rem;
    }
    ul li{position: relative;}
    
    .submitted{
        display: flex;
        
    }
    .remark{
        color: #aaa;
    }
}

.links, .bio{
    li{margin:0px;background-color: transparent !important;font-size: 16px;padding:2px 10px;}
}

.btn-mode{
    position: fixed;
    height: 50px;
    width: 50px;
    background: #666;
    bottom:100px;
    right: 20px;
    border-radius: 50px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50%;
    box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
    cursor: pointer;

}
.btn-mode.todark{
    background-color: white;
}
strong{
    // color: $main-color;
    position: relative;
    border-bottom: 2px solid $main-color;
}

a{
    // color: $main-color;
    color: black;
    position: relative;
    text-decoration: none;
    
}

a:hover{
    border-bottom: 1px solid $main-color;
    color: $main-color !important;
}

.tolight{
    display: none;
}
.todark{
    display: block;
}

.news, .paper, .education, .honor{
    li:hover{
        transform: scale(1.01);
        box-shadow: 0px 10px 10px -5px rgba(0,0,0,0.1);
    }
}

.clustrmaps-map-control{
    display: none !important
}